<template>
  <div class="contact block">
    <div class="container">
      <v-row>
        <v-col sm="3" class="d-flex align-center pl-5">
          <div class="pl-2">
            <div class="contact-box">
              <div class="contact-title">{{ $t('phone') }}:</div>

              <div class="contact-value">
                <a class="contact-value-tel" :href="'tel: +380960064175'">+380960064175</a>
              </div>
            </div>
            <div class="contact-box">
              <div class="contact-title">{{ $t('email') }}:</div>
              <div class="contact-value">support@agroxy.com</div>
            </div>
          </div>
        </v-col>
        <v-col sm="4" class="contact-block">
          <a href="https://www.facebook.com/agroxyua/" class="social-link">
            <v-icon class="social-icon">mdi-facebook</v-icon>
          </a>
          <a href="https://twitter.com/agroxy_com" class="social-link">
            <v-icon class="social-icon">mdi-twitter</v-icon>
          </a>
          <a href="https://www.linkedin.com/company/agroxy-com/" class="social-link">
            <v-icon class="social-icon">mdi-linkedin</v-icon>
          </a>
        </v-col>
        <v-col sm="5">
          <v-form
            ref="form"
            v-model="valid"
            style="padding: 15px;"
            class="sign-in-form"
            @submit.prevent="submitContactForm"
          >
            <v-text-field
              v-model="form.email"
              type="email"
              :rules="rules.emailRules"
              label="Email"
              color="#F46C28"
              autocomplete="email"
              required
              background-color="grey lighten-2"
            />
            <v-textarea
              v-model="form.body"
              :rules="[rules.required]"
              background-color="grey lighten-2"
              required
              color="#F46C28"
              :label="$t('message')"
              rows="3"
            ></v-textarea>
            <div class="d-flex justify-center">
              <v-btn
                :disabled="!valid"
                depressed
                color="#F46C28"
                type="submit"
              >
                {{ $t('send') }}
              </v-btn>
            </div>
          </v-form>
        </v-col>
      </v-row>
    </div>
    <v-snackbar
      v-model="snackbar"
      :timeout="3000"
      rounded="pill"
      :top="true"
    >
      Success
      <template #action="{ attrs }">
        <v-btn text v-bind="attrs" @click="snackbar = false">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </template>
    </v-snackbar>
  </div>
</template>

<script>
import qs from 'qs'
import axios from 'axios'
export default {
  name: 'Contact',
  data: () => ({
    form: {
      email: '',
      body: '',
      site_name: 'agroxy.com'
    },
    snackbar: false,
    valid: false,
    rules: {
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(v) || 'E-mail must be valid'
      ],
      required: value => !!value || 'Required.'
    },
  }),
  methods: {
    submitContactForm () {
      axios
        .post('https://api.agroxy.africa/en/v2/contact-form/create-issue', qs.stringify({ ContactForm: this.form }))
        .then(() => {
          this.snackbar = true
          this.form = {
            email: '',
            body: '',
            site_name: 'agroxy.com'
          }
        })
    }
  }
}
</script>

