<template>
  <div class="news block--dark">
    <h1 class="block-title">{{ $t('news') }}</h1>
    <div>
      <!--<div class="filters">-->
        <!--<button @click="togglePostType('africanNews')" ref="filter" :class="{'filter-active' : visiblePostCategories.includes('africanNews')}">African</button>-->
        <!--<button @click="togglePostType('ukrainianNews')" ref="filter" :class="{'filter-active' : visiblePostCategories.includes('ukrainianNews')}">Ukrainian</button>-->
        <!--<button @click="togglePostType('internationalNews')" ref="filter" :class="{'filter-active' : visiblePostCategories.includes('internationalNews')}">International</button>-->
      <!--</div>-->

      <transition-group class="grid" tag="div" name="transition--post-filter-" @enter="resetGrid" @after-leave="resetGrid">
        <p key="noPosts" v-if="filteredPosts.length < 1" class="no-posts">Choose a category from the options above</p>
        <a
          v-for="post in shownPosts"
          :key="post.date.toString()"
          :href="post.img.slug"
          target="_blank"
          :style="{ height: `${post.height}rem` }"
          class="grid-item card-wrap"
        >
          <v-card
            class="mx-auto card"
            outlined
          >
            <v-img
              class="white--text align-end card-img"
              width="100%"
              height="100%"
              :src="post.img.thumbnail_base_url + post.img.thumbnail_path"
            >
              <!--<v-card-title>Main news title</v-card-title>-->
              <v-card-text class="text">
                <div class="text-title">{{ post.img.title }}</div>
                <div class="text-main" v-html="post.img.short_text">
                  <!--{{ post.img.short_text }}-->
                </div>
              </v-card-text>
            </v-img>
          </v-card>
        </a>
      </transition-group>
      <div class="text-center" v-if="$vuetify.breakpoint.mdAndDown">
        <v-btn
          class="mb-5"
          outlined
          color="white"
          @click="showMore"
        >
          Show more
        </v-btn>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
// import NewsCard from '@/components/NewsCard';
import Masonry from 'masonry-layout'
export default {
  name: 'News',
  data: () => ({
    africanNews: [],
    ukrainianNews: [],
    internationalNews: [],
    // visiblePostCategories: ['puppies','food','middleEarth'],
    visiblePostCategories: ['africanNews','ukrainianNews','internationalNews'],
    shownPostsNumber: 4,
  }),

  computed: {
    allPosts() {
      // let randomDate = (start, end) => {
      //   return new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
      // }

      let setPostsInfo = (source,categoryName) => {
        let posts = source.map((image) => {
          return {
            img: image,
            category: categoryName,
            date: new Date(image.published_at),
            height: Math.floor(Math.random() * (25 - 8)) + 13
          }
        })

        return posts
      }


      let allPosts = [...setPostsInfo(this.africanNews,'africanNews'), ...setPostsInfo(this.ukrainianNews,'ukrainianNews'), ...setPostsInfo(this.internationalNews,'internationalNews')]

      let allPostsSortedByDate = allPosts.sort((a, b) => {
        return a.date > b.date ? -1 : a.date < b.date ? 1 : 0
      })

      return allPostsSortedByDate
    },

    filteredPosts() {
      let filteredPosts = this.allPosts.filter(post => (
        this.visiblePostCategories.indexOf(post['category']) > -1
      ))

      let filteredPostsSortedByDate = filteredPosts.sort((a, b) => {
        return a.date > b.date ? -1 : a.date < b.date ? 1 : 0
      })
      return filteredPostsSortedByDate
    },
    shownPosts() {
      if (this.$vuetify.breakpoint.lgAndUp) {
        return this.filteredPosts
      } else {
        return this.filteredPosts.slice(0, this.shownPostsNumber)
      }
    }
  },
  mounted() {
    let grid = document.querySelector('.grid')
    let msnry = new Masonry( grid, { itemSelector: '.grid-item' } )
    console.log(msnry)
    // axios
    //   .get('https://api.agroxy.africa/en/v2/article/index')
    //   .then((response) => {
    //     response.data.items.forEach((element) => {
    //       element.thumbnail_base_url = element.thumbnail_base_url + '/'
    //       element.slug = 'https://agroxy.africa/article/' + element.slug
    //     })
    //     this.africanNews = response.data.items
    //     console.log(this.africanNews)
    //   })
    // axios
    //   .post('https://api.agroxy.africa/en/v2/article/international-article')
    //   .then((response) => {
    //     response.data.forEach((element) => {
    //       element.thumbnail_base_url = element.thumbnail_base_url + '/'
    //       element.slug = 'https://agroxy.africa/article/' + element.slug
    //     })
    //     this.internationalNews = response.data
    //     console.log(this.internationalNews)
    //   })
    axios
      .get('https://api.agroxy.com/v2/article/index')
      .then((response) => {
        response.data.items.forEach((element) => {
          element.slug = 'https://agroxy.com.ua/article/' + element.slug
        })
        this.ukrainianNews = response.data.items
        console.log(this.ukrainianNews)
      })
  },
  methods: {
    resetGrid() {
      let gridEl = document.querySelector('.grid')

      Masonry.data(gridEl).reloadItems()

      this.$nextTick(() => {
        Masonry.data(gridEl).layout()
      })
    },
    showMore() {
      this.shownPostsNumber = this.shownPostsNumber+4
    }
  }
}
</script>

