<template>
  <div class="news block block--dark">
    <div class="container">
      <h1 class="block-title pt-0">{{ $t('pressAboutUs') }}</h1>
      <div class="block-main">
        <v-row>
          <v-col sm="4" v-for="pressItem in pressList" :key="pressItem.id">
            <PressCard :id="pressItem.id" :press="pressItem"></PressCard>
          </v-col>
        </v-row>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import PressCard from '@/components/PressCard';
export default {
  name: 'News',
  data: () => ({
    pressList: []
  }),
  components: {
    PressCard,
  },
  mounted() {
    axios
      .post('https://api.agroxy.africa/en/v2/article/press-about-us')
      .then((response) => {
        this.pressList = response.data
      })
  }
}
</script>

