<script>
import i18n from './../plugins/i18n';
export default {
  name: 'Header',
  data: () => ({
    drawer: false,
    languages: [
      { flag: 'gb', language: 'en', title: 'English' },
      { flag: 'ua', language: 'ua', title: 'Українська' },
      { flag: 'ru', language: 'ru', title: 'Русский' },
      { flag: 'tz', language: 'sw', title: '' },
      { flag: 'fr', language: 'fr', title: '' },
      { flag: 'de', language: 'de', title: '' },
    ],
    currentLang: { flag: 'gb', language: 'en', title: 'English' },
  }),
  computed: {
    Items() {
      return [
        {
          title: this.$t('aboutUs'),
          to: '#about',
          icon: 'mdi-information-outline'
        },
        {
          title: this.$t('news'),
          to: '#news',
          icon: 'mdi-newspaper-variant-outline'
        },
        {
          title: this.$t('team'),
          to: '#team',
          icon: 'mdi-account-group'
        },
        {
          title: this.$t('press'),
          to: '#press',
          icon: 'mdi-newspaper-variant'
        },
        {
          title: this.$t('partners'),
          to: '#partners',
          icon: 'mdi-handshake'
        },
        {
          title: this.$t('contact'),
          to: '#contact',
          icon: 'mdi-phone'
        }
      ]
    },
  },
  methods: {
    changeLocale(locale) {
      i18n.locale = locale.language;
      this.currentLang = locale
    }
  }
}
</script>

